import React, { useState } from "react";
import CarsFilter from "./CarsFilter";
import CarItem from "./CarItem";
import CarsFilterMobile from "./CarsFilterMobile";

const CarsPage = ({ cars }) => {
  const [filterState, setFilterState] = useState("electro");
  const [inStockMode, setInStockMode] = useState(true);
  const mapToCarsFilter = cars.map((car) => {
    if (filterState == "electro") {
      return <CarItem state={cars} car={car} />;
    } else if (car.filter === filterState) {
      return <CarItem state={cars} car={car} />;
    }
  });
  return (
    <section className="cars">
      <div className="container">
        <h2 className="cars__suptitle my-3">
               
        </h2>
        <h1 className="cars__title my-3"></h1>
        <CarsFilter
          setFilterState={setFilterState}
          setInStockMode={setInStockMode}
        />
        <CarsFilterMobile
          setFilterState={setFilterState}
          setInStockMode={setInStockMode}
        />
        <div className="cars__list">
          <div className="row gy-3">
            {inStockMode
              ? mapToCarsFilter
              : cars.map((car) => {
                  if (car.inStock) {
                    return <CarItem state={cars} car={car} />;
                  }
                })}
          </div>
        </div>
      </div>
    </section>
  );
};

export default CarsPage;
