const carReducer = (state = initialState, action) => {
  switch (action.type) {
    case CURRENT_PRICE: {
      return { ...state, currentPrice: action.sum };
    }

    default:
      return state;
  }
};

export const calculatorProduct = (sum) => ({
  type: CURRENT_PRICE,
  sum,
});

export default carReducer;
